import './styles.css';
import Handlebars from 'handlebars'
import jsondata from './menu.json'
console.log(jsondata);

console.log(Handlebars);
const elementInfo = document.querySelector('#item-template').innerHTML;
const template = Handlebars.compile(elementInfo)
const body = document.querySelector('body')
console.log(body);
const input = document.querySelector('.switch__input')
console.log(input);

let theme = localStorage.getItem('theme')


const ul = document.querySelector('.js-menu')
let markup = ""
const createtemplate = function(){
    for(const element of jsondata){
        markup = template(element)
        ul.innerHTML += markup
    }
}
createtemplate()

const Theme = {
    LIGHT: 'light-theme',
    DARK: 'dark-theme',
  };

  const setLight = function () {
    body.classList.add(Theme.LIGHT);
    body.classList.remove(Theme.DARK);
  };
  
  const setDark = function () {
    body.classList.remove(Theme.LIGHT);
    body.classList.add(Theme.DARK);
  };

if(theme === 'light')input.checked = false
else if(theme === 'dark')input.checked = true
else input.checked = false
if(input.checked)setDark()
else setLight()

input.addEventListener('change',() => {
    if(input.checked){
        setDark()
        localStorage.setItem('theme', 'dark')
        
    }
else {
    setLight()
    localStorage.setItem('theme', 'light')
}
})


